// Resetting styles.
* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  overflow: hidden;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;

  @media screen and (max-width: 991.98px) {
    font-size: 13px;
  }

  @media screen and (max-width: 767.98px) {
    font-size: 12px;
  }

  @media screen and (max-width: 479.98px) {
    font-size: 10px;
  }
}
input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

// General styles.
body {
  @media screen and (max-width: 991.98px) {
    min-height: 580px;
    height: 100%;
  }

  @media screen and (max-width: 479.98px) {
    background-position: initial;
    height: auto;
  }
}

.wrapper {
  font-family: "Josefin Slab", serif;
  background-image: url("../public/images/halloween.webp");
  height: 100vh;
  min-height: 480px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.3);
  color: #fff;
  -webkit-transition: background-image 0.7s ease-in-out;
  transition: background-image 0.7s ease-in-out;

  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  text-align: center;
  overflow-x: hidden;
}
